import React ,{Component}from 'react'
import { Grid, Cell, TextField} from 'react-md'
import firebaseApp from '../firebase/config'
import firebase from 'firebase'
import {ToastsContainer, ToastsStore} from 'react-toasts';
import './userSign.css'

class UserSignin extends Component{
    constructor(){
        super()
        this.state={
            Signup:'',
            Signin:'none',
            SinginEmail:"",
            SinginPassword:"",
            SingupUser:"",
            SingupEmail:"",
            SingupPassword:"",
            SignUpAreaStyle:"15%",
            SignInAreaStyle:'19.5%',
            ErrorTextOfSignUpPassowrd:"",
            ErrorTextOfSignUpUserName:'',
            ErrorTextOfSignUpEmail:'',
            ErrorTextOfSignInPassowrd:'',
            ErrorTextOfSignInEmail:'',
            HandleErrorTextOfSignUpPassowrd:false,
            HandleErrorTextOfSignUpUserName:false,
            HandleErrorTextOfSignUpPassowrd:false,
            HandleErrorTextOfSignInPassword:false,
            HandleErrorTextOfSignInEmail:false,
        }

        // this is used to get the auth from firebase config
        
        this.auth = firebaseApp.auth()


        // all of this is used just to debug some functions

        this.handleSignInChanges = this.handleSignInChanges.bind(this)
        this.handleSignUpChanges = this.handleSignUpChanges.bind(this)
        this.handleSinginEmail = this.handleSinginEmail.bind(this)
        this.handleSinginPassword = this.handleSinginPassword.bind(this)
        this.handleSingupEmail = this.handleSingupEmail.bind(this)
        this.handleSingupPassword = this.handleSingupPassword.bind(this)
        this.handleSingupUser = this.handleSingupUser.bind(this)
        this.handleSignIn = this.handleSignIn.bind(this)
        this.handleSignUP = this.handleSignUP.bind(this)
    }

    componentDidMount(){

        // this will check wich user is logged in and will update userdisplay name with the value from input box

        this.auth.onAuthStateChanged((user) =>{
            if (user) {
              user.updateProfile({

                // this will update user displayName and photo

                displayName: this.state.SingupUser,
                photoURL: "",

              }).then(() => {
                
                // this will reset Sign up user value

                this.setState({
                    SingupUser:""
                })

              }).catch( 

                // this will be alerted when an err happen

                e => ToastsStore.error(e.message)
              );     

            }
        });
    }


    handleSignInChanges(){
        this.setState({

            // this will reset state and hide Sign up box

            Signup:'none',
            Signin:'',
            SinginEmail:"",
            SinginPassword:"",
            SingupUser:"",
            SingupEmail:"",
            SingupPassword:"",
            SignUpAreaStyle:"15%",
            SignInAreaStyle:'19.5%',
            ErrorTextOfSignUpPassowrd:"",
            ErrorTextOfSignUpUserName:'',
            ErrorTextOfSignUpEmail:'',
            ErrorTextOfSignInPassowrd:'',
            ErrorTextOfSignInEmail:'',
            HandleErrorTextOfSignUpPassowrd:false,
            HandleErrorTextOfSignUpUserName:false,
            HandleErrorTextOfSignUpPassowrd:false,
            HandleErrorTextOfSignInPassword:false,
            HandleErrorTextOfSignInEmail:false,
        })
    }

    handleSignUpChanges(){

        // this will reset state and hide Sign in box

        this.setState({
            Signup:'',
            Signin:'none',
            SinginEmail:"",
            SinginPassword:"",
            SingupUser:"",
            SingupEmail:"",
            SingupPassword:"",
            SignUpAreaStyle:"15%",
            SignInAreaStyle:'19.5%',
            ErrorTextOfSignUpPassowrd:"",
            ErrorTextOfSignUpUserName:'',
            ErrorTextOfSignUpEmail:'',
            ErrorTextOfSignInPassowrd:'',
            ErrorTextOfSignInEmail:'',
            HandleErrorTextOfSignUpPassowrd:false,
            HandleErrorTextOfSignUpUserName:false,
            HandleErrorTextOfSignUpPassowrd:false,
            HandleErrorTextOfSignInPassword:false,
            HandleErrorTextOfSignInEmail:false,
        })
    }

    handleSinginEmail(emailValue){

        //this will update state with email value with Sign in method

        this.setState({
            SinginEmail: emailValue
        })
    }

    handleSinginPassword(passwordValue){

        //this will update state with password value with Sign in method

        this.setState({
            SinginPassword: passwordValue
        })
    }

    handleSingupEmail(emailValue){

        // this will update state with email value in Sign up method

        this.setState({
            SingupEmail: emailValue
        })
    }

    handleSingupPassword(passwordValue){

        // this will update state with password value in sign up method

        this.setState({
            SingupPassword : passwordValue
        })
    }

    handleSingupUser(userValue){

        //this will update state with user value in sign up method

        this.setState({
            SingupUser: userValue
        })
    }


    handleSignIn(){
        this.email = this.state.SinginEmail
        this.password = this.state.SinginPassword

        const SignInPassowrdErrMsg = 'There is an error occurred in Password input box.Try to check what is wrong'
        const SignInEmailErrMsg = 'There is an error occurred in Email input box.Try to check what is wrong'

        if(this.email==="" && this.password===""){

            //This checks if the email and password are inserted correctly.If not an err will be sent

            ToastsStore.error('You need to fill the input box to get SignIn')

            this.setState({
                HandleErrorTextOfSignInEmail:true,
                HandleErrorTextOfSignInPassword:true,
                ErrorTextOfSignInEmail:SignInEmailErrMsg,
                ErrorTextOfSignInPassowrd:SignInPassowrdErrMsg,
                SignInAreaStyle:'14%',
            })
        }else if(this.email===""){

            //This checks if the email is inserted correctly.If not an err will be sent

            ToastsStore.error('You need to fill the input box to get SignIn')

            this.setState({
                HandleErrorTextOfSignInEmail:true,
                HandleErrorTextOfSignInPassword:false,
                ErrorTextOfSignInEmail:SignInEmailErrMsg,
                ErrorTextOfSignInPassowrd:"",
                SignInAreaStyle:'16.5%',
            })
        }else if(this.password===""){

            //This checks if the password are inserted correctly.If not an err will be sent

            ToastsStore.error('You need to fill the input box to get SignIn')

            this.setState({
                HandleErrorTextOfSignInEmail:false,
                HandleErrorTextOfSignInPassword:true,
                ErrorTextOfSignInEmail:"",
                ErrorTextOfSignInPassowrd:SignInPassowrdErrMsg,
                SignInAreaStyle:'16.5%',
            })
        }else{

            
            const promise = this.auth.signInWithEmailAndPassword(this.email,this.password)
            promise.catch(e => ToastsStore.error(e.message))

            promise.catch(e => {
                //This checks if The email address is badly formatted.If it is an err will be sent

            if(e.message === "The email address is badly formatted."){
                this.setState({
                    HandleErrorTextOfSignInEmail:true,
                    HandleErrorTextOfSignInPassword:false,
                    ErrorTextOfSignInEmail:"The email address is badly formatted.",
                    ErrorTextOfSignInPassowrd:"",
                    SignInAreaStyle:'17.5%',
                })
            }
            
            //this checks if There is no user record corresponding to this identifier.If it is an err will be sent

            else if(e.message ==="There is no user record corresponding to this identifier. The user may have been deleted."){
                this.setState({
                    HandleErrorTextOfSignInEmail:true,
                    HandleErrorTextOfSignInPassword:true,
                    ErrorTextOfSignInEmail:"There is no user record corresponding to this identifier. The user may have been deleted.",
                    ErrorTextOfSignInPassowrd:"There is no user record corresponding to this identifier. The user may have been deleted.",
                    SignInAreaStyle:'13.5%',
                })
            }

            //this checks if The password is invalid or the user does not have a password.if it is an err will be sent

            else if(e.message ==="The password is invalid or the user does not have a password."){
                this.setState({
                    HandleErrorTextOfSignInEmail:true,
                    HandleErrorTextOfSignInPassword:true,
                    ErrorTextOfSignInEmail:"The password is invalid or the user does not have a password.",
                    ErrorTextOfSignInPassowrd:"The password is invalid or the user does not have a password.",
                    SignInAreaStyle:'14%',
                })
            }
        })

        //this there is no err found.User will Log In Successfully

            if(promise.catch() === -1){
                this.setState({
                    SinginEmail:"",
                    SinginPassword:"",
                })   
            }
        }
        
    }


    handleSignUP(){

        this.email = this.state.SingupEmail
        this.password = this.state.SingupPassword
        this.username = this.state.SingupUser

        const SignUpPassowrdErrMsg = 'There is an error occurred in Password input box.Try to check what is wrong'
        const SignUpEmailErrMsg = 'There is an error occurred in Email input box.Try to check what is wrong'
        const SignUpUserNameErrMsg = 'There is an error occurred in UserName input box.Try to check what is wrong'

        if(this.email==="" && this.password===""&& this.username===""){

        //This checks if the password and email and username were correctly validated before storing in the database.If not sends err
            
            ToastsStore.error('You need to fill the input box to get SignUp')

            this.setState({
                HandleErrorTextOfSignUpPassowrd:true,
                HandleErrorTextOfSignUpUserName:true,
                HandleErrorTextOfSignUpEmail:true,
                ErrorTextOfSignUpPassowrd:SignUpPassowrdErrMsg,
                ErrorTextOfSignUpUserName:SignUpUserNameErrMsg,
                ErrorTextOfSignUpEmail:SignUpEmailErrMsg,
                SignUpAreaStyle:"8%",
            })
        }
        
        else if(this.password==="" && this.username===""){

        //This checks if the password and username were correctly validated before storing in the database.If not sends err
            
            ToastsStore.error('You need to fill the input box to get SignUp')

            this.setState({
                HandleErrorTextOfSignUpPassowrd:true,
                HandleErrorTextOfSignUpUserName:true,
                HandleErrorTextOfSignUpEmail:false,
                ErrorTextOfSignUpPassowrd:SignUpPassowrdErrMsg,
                ErrorTextOfSignUpUserName:SignUpUserNameErrMsg,
                ErrorTextOfSignUpEmail:"",
                SignUpAreaStyle:"11%",
            })
        }

        else if(this.email==="" && this.username===""){

            //This checks if the email and username were correctly validated before storing in the database.If not sends err
            
            ToastsStore.error('You need to fill the input box to get SignUp')
            this.setState({
                HandleErrorTextOfSignUpPassowrd:false,
                HandleErrorTextOfSignUpUserName:true,
                HandleErrorTextOfSignUpEmail:true,
                ErrorTextOfSignUpPassowrd:"",
                ErrorTextOfSignUpUserName:SignUpUserNameErrMsg,
                ErrorTextOfSignUpEmail:SignUpEmailErrMsg,
                SignUpAreaStyle:"11%",
            })
        }

        else if(this.password==="" && this.email===""){

            //This checks if the  email and password were correctly validated before storing in the database.If not sends err

            ToastsStore.error('You need to fill the input box to get SignUp')
            this.setState({
                HandleErrorTextOfSignUpPassowrd:true,
                HandleErrorTextOfSignUpUserName:false,
                HandleErrorTextOfSignUpEmail:true,
                ErrorTextOfSignUpPassowrd:SignUpPassowrdErrMsg,
                ErrorTextOfSignUpUserName:"",
                ErrorTextOfSignUpEmail:SignUpEmailErrMsg,
                SignUpAreaStyle:"11%",
            })
        }

        else if(this.password===""){

            //This checks if the password was correctly validated before storing in the database.If not sends err

            ToastsStore.error('You need to fill the input box to get SignUp')
            this.setState({
                HandleErrorTextOfSignUpPassowrd:true,
                HandleErrorTextOfSignUpUserName:false,
                HandleErrorTextOfSignUpEmail:false,
                ErrorTextOfSignUpPassowrd:SignUpPassowrdErrMsg,
                ErrorTextOfSignUpUserName:"",
                ErrorTextOfSignUpEmail:"",
                SignUpAreaStyle:"13%",
            })
        }

        else if(this.email===""){

            //This checks if the email was correctly validated before storing in the database.If not sends err

            ToastsStore.error('You need to fill the input box to get SignUp')
            this.setState({
                HandleErrorTextOfSignUpPassowrd:false,
                HandleErrorTextOfSignUpUserName:false,
                HandleErrorTextOfSignUpEmail:true,
                ErrorTextOfSignUpPassowrd:"",
                ErrorTextOfSignUpUserName:"",
                ErrorTextOfSignUpEmail:SignUpEmailErrMsg,
                SignUpAreaStyle:"13%",
            })
        }

        else if(this.username===""){

            //This checks if the username was correctly validated before storing in the database.If not sends err

            ToastsStore.error('You need to fill the input box to get SignUp')
            this.setState({
                HandleErrorTextOfSignUpPassowrd:false,
                HandleErrorTextOfSignUpUserName:true,
                HandleErrorTextOfSignUpEmail:false,
                ErrorTextOfSignUpPassowrd:"",
                ErrorTextOfSignUpUserName:SignUpUserNameErrMsg,
                ErrorTextOfSignUpEmail:"",
                SignUpAreaStyle:"13%",
            })
        }else{

            const promise = this.auth.createUserWithEmailAndPassword(this.email,this.password)
            promise.catch(e => ToastsStore.error(e.message))
            promise.catch(e => {
                //this checks if the email address is badly formatted.
                if(e.message === 'The email address is badly formatted.'){
                this.setState({
                    HandleErrorTextOfSignUpPassowrd:false,
                    HandleErrorTextOfSignUpUserName:false,
                    HandleErrorTextOfSignUpEmail:true,
                    ErrorTextOfSignUpPassowrd:"",
                    ErrorTextOfSignUpUserName:"",
                    ErrorTextOfSignUpEmail:'The email address is badly formatted.',
                    SignUpAreaStyle:"14%",
                })
            }
            //this checks if The email address is already in use by another account.
            else if(e.message === 'The email address is already in use by another account.'){
                this.setState({
                    HandleErrorTextOfSignUpPassowrd:false,
                    HandleErrorTextOfSignUpUserName:false,
                    HandleErrorTextOfSignUpEmail:true,
                    ErrorTextOfSignUpPassowrd:"",
                    ErrorTextOfSignUpUserName:"",
                    ErrorTextOfSignUpEmail:'The email address is already in use by another account.',
                    SignUpAreaStyle:"14%",
                })
            }
            //this checks if Password should be at least 6 characters
            else if(e.message === 'Password should be at least 6 characters'){
                this.setState({
                    HandleErrorTextOfSignUpPassowrd:true,
                    HandleErrorTextOfSignUpUserName:false,
                    HandleErrorTextOfSignUpEmail:false,
                    ErrorTextOfSignUpPassowrd:"Password should be at least 6 characters",
                    ErrorTextOfSignUpUserName:"",
                    ErrorTextOfSignUpEmail:'',
                    SignUpAreaStyle:"14%",
                })
            }
        })
  
            //this there is no err found.User will Log In Successfully

            if(promise.catch() === -1){
                this.setState({
                        SingupEmail:"",
                        SingupPassword:"",
                })
            }
        }
    }

    render(){
        const SignInBoxCss ={
            display: this.state.Signup
        }
        const SignUpBoxCss ={
            display: this.state.Signin
        }
        const SignUpAreaStyle={
            padding: this.state.SignUpAreaStyle
        }
        const SingInArea ={
            padding: this.state.SignInAreaStyle
        }
        return(
            <div>
                <ToastsContainer store={ToastsStore} className="ToasterLeftTopSide"/>
                
                <Grid className='UserSigninBox' style={SignInBoxCss}>
                    <Cell size={8}>
                        <div style={SingInArea}>
                            <h2>Sign in To The Site</h2>
                            <TextField
                                id="floating-center-title"
                                type="email"
                                label="Email"
                                lineDirection="center"
                                placeholder="Enter your email..."
                                value={this.state.SinginEmail}
                                onChange={this.handleSinginEmail}
                                className="md-cell md-cell--bottom inputfill"
                                error={this.state.HandleErrorTextOfSignInEmail}
                                errorText={this.state.ErrorTextOfSignInEmail}
                            />
                            <TextField
                                id="floating-password"
                                type="password"
                                label="Password"
                                lineDirection="center"
                                placeholder="Enter your password..."
                                value={this.state.SinginPassword}
                                onChange={this.handleSinginPassword}
                                className="md-cell md-cell--bottom inputfill"
                                error={this.state.HandleErrorTextOfSignInPassword}
                                errorText={this.state.ErrorTextOfSignInPassowrd}
                            />
                            <br/><br/>
                            <button className="Submitebutton" onClick={this.handleSignIn}>SIGN IN</button>
                        </div>
                    </Cell>
                    <Cell size={4} className='SingInTextArea' >
                            <div className="SignText">
                                <h3>Welcome back!</h3>
                                <br/>
                                <p>this website is just for testing so dont enter any of your persinal details please</p>
                                <br/>
                                <button className='SignUpbutton' onClick={this.handleSignInChanges}>SIGN UP</button>
                            </div>
                    </Cell>
                </Grid>

                <Grid className='UserSignUpBox' style={SignUpBoxCss}>
                    <Cell size={4} className='SingInTextArea' >
                            <div className="SignText">
                                <h3>Hello Friend!</h3>
                                <br/>
                                <p>this website doesn't secure any kind of password or persinal infos.It saves date in plane text so please don't integrate your persinal infos here</p>
                                <br/>
                                <button className='SignUpbutton' onClick={this.handleSignUpChanges}>SIGN IN</button>
                            </div>
                    </Cell>
                    <Cell size={8}>
                        <div style={SignUpAreaStyle}>
                            <h2>Create Account</h2>
                            <TextField
                                id="floating-center-title"
                                label="UserName"
                                lineDirection="center"
                                placeholder="Enter your username..."
                                value={this.state.SingupUser}
                                onChange={this.handleSingupUser}
                                className="md-cell md-cell--bottom inputfill"
                                error={this.state.HandleErrorTextOfSignUpUserName}
                                errorText={this.state.ErrorTextOfSignUpUserName}
                            />
                            <TextField
                                id="floating-center-title"
                                type="email"
                                label="Email"
                                lineDirection="center"
                                placeholder="Enter your email..."
                                value={this.state.SingupEmail}
                                onChange={this.handleSingupEmail}
                                className="md-cell md-cell--bottom inputfill"
                                error={this.state.HandleErrorTextOfSignUpEmail}
                                errorText={this.state.ErrorTextOfSignUpEmail}
                            />
                            <TextField
                                id="floating-center-password"
                                type="password"
                                label="Password"
                                lineDirection="center"
                                placeholder="Enter your password..."
                                value={this.state.SingupPassword}
                                onChange={this.handleSingupPassword}
                                className="md-cell md-cell--bottom inputfill"
                                error={this.state.HandleErrorTextOfSignUpPassowrd}
                                errorText={this.state.ErrorTextOfSignUpPassowrd}
                            />
                            <br/><br/>
                            <button className="Submitebutton" onClick={this.handleSignUP}>SIGN UP</button>
                        </div>
                    </Cell>
                </Grid>
            </div>
        )
    }

}

export default UserSignin 