import React ,{Component}from 'react'
import PropTypes from 'prop-types';
import ProfileSettings from './profilesettings'
import {
    Avatar,
    FontIcon,
    AccessibleFakeButton,
    IconSeparator,
    DropdownMenu,
  } from 'react-md';
import firebaseApp from '../firebase/config'
import firebase from 'firebase'
import {ToastsContainer, ToastsStore} from 'react-toasts';
import Loader from 'react-loader-spinner'
import './dashboard.css'


class Homepage extends Component{

    constructor(){
        super()
        this.state={
            userImgUrl:'',
            userName:"",
            userIconFirstLetter:'',
            loadingDash:false,
            showSettings:false,
            IconBackgroundColor:'white',
            backgroundColor:'',
        }

        // this is used to get the data from firebase related to loadingDash
        this.dataLoadingDash = firebaseApp.database().ref().child('loadingDash');

        this.auth = firebaseApp.auth()
        //this checks current user
        this.user = firebaseApp.auth().currentUser

        //this helps debugging some fanctions
        this.handleLogout = this.handleLogout.bind(this)
        this.handleSettings = this.handleSettings.bind(this)
        this.handleSpanSetting = this.handleSpanSetting.bind(this)
        

    }

    componentDidMount(){
        //this is the loading screen it fetchs data from firebase and updates with the new value time out used just to make it little bit longer 
        
        setTimeout(() => {this.dataLoadingDash.on('value' , snap => {
            this.setState({
            loadingDash: snap.val()
            })

                this.auth.onAuthStateChanged((user) =>{
                    if (user) {
                        if (this.user != null) {

                            //this fetchs user details from firebase
                            this.name = this.user.displayName
                            this.email = this.user.email
                            this.uid = this.user.uid
                            this.photoURL = this.user.photoURL 
            
                            //this gets the first letter from the username to update it in user icon
                            this.userIconFirstLetter =  this.name.toString().charAt(0).toUpperCase()
                
                            this.setState({
                                userName: this.name,
                                userIconFirstLetter: this.userIconFirstLetter,
                                userImgUrl:this.photoURL,
                              })
                              //this gets triggered in everything went fine 
                              ToastsStore.success('Logged In Successfully')
                          }
                      }
                })
          })},4500)
    }
    

    handleSettings(){
        //this handles the opening of the settings box

        this.setState({
            showSettings:true
        })
    }
    handleSpanSetting(){
        //this handles the closing of the settings box

        this.setState({
            showSettings:false
        })
    }

    handleLogout(){
        //this just signs out the user when the function is called

        this.auth.signOut()
    }

    render(){
        //this check if the web app is connected to the firebase or no if it isnt it will trigger a loading screen
       
        if(this.state.loadingDash === false){
            // this is the loading screen

            return(
                    <div className='LoadingScrean'>
                        <Loader type="Triangle" color="rgb(72, 219, 138)" height={80} width={80}/>
                    </div>
                );
            
        }
        // this gets triggered when the web app coonects to the firebase

        else{

            // this is the setting box it gets triggered when handleSettings() gets triggered

            const ProfileSettingsPopup = () => {
                if(this.state.showSettings === true){
                   return(
                        <ProfileSettings handleSpanSetting={this.handleSpanSetting} divid="myModal"/>
                   );
                }else{
                    return(null)
                }
            }

            //this handles the user icon.Firstly it check if the user got an img or no.If not it will set it to default
            // it will check the user name and takes the first letter of it

            const UserIcon = e =>{
                if(this.state.userImgUrl === null){
                    return(<Avatar random className="UserIconLogo">{this.state.userIconFirstLetter}</Avatar>)
                }else{
                    return(<Avatar src={this.state.userImgUrl} role="presentation"/>)
                }
            };
            
            //this handles the user dropdown list.It is taken from the module react-md

            const AccountMenu = ({ simplifiedMenu }) => (
                <div>
                    <div className='UserIcon navBar'>
                        <DropdownMenu
                        id={`${!simplifiedMenu ? 'smart-' : ''}avatar-dropdown-menu`}
                        menuItems={[
                            'Preferences', 
                            <div 
                            role="button" 
                            className="md-fake-btn md-pointer--hover md-fake-btn--no-outline md-list-tile md-text Hovered" 
                            onClick={this.handleSettings}>Edit Profile</div>, 
                            { divider: true }, 
                            <div 
                            role="button" 
                            className="md-fake-btn md-pointer--hover md-fake-btn--no-outline md-list-tile md-text Hovered" 
                            onClick={this.handleLogout}>Log Out</div>,
                        ]}
                        anchor={{
                            x: DropdownMenu.HorizontalAnchors.CENTER,
                            y: DropdownMenu.VerticalAnchors.OVERLAP,
                        }}
                        position={DropdownMenu.Positions.TOP_RIGHT}
                        animationPosition="below"
                        sameWidth
                        simplifiedMenu={simplifiedMenu}
                        >
                        <AccessibleFakeButton
                            component={IconSeparator}
                            iconBefore
                            label={
                            <IconSeparator label={this.state.userName}>
                                <FontIcon>arrow_drop_down</FontIcon>
                            </IconSeparator>
                            }
                        >
                            <UserIcon/>
                        </AccessibleFakeButton>
                        </DropdownMenu>
                    </div>
                </div>
              );
              
              AccountMenu.propTypes = {
                simplifiedMenu: PropTypes.bool,
              };

              //^^this handles the user dropdown list.It is taken from the module react-md^^

            return(
                
                <div>
                    <ToastsContainer store={ToastsStore} className="ToasterLeftTopSide"/>
                    <ProfileSettingsPopup IconBackgroundColor={this.state.IconBackgroundColor}/>
                    <AccountMenu />
                </div>
            );
        }
    }


}

export default Homepage